<template>
  <div>
    {{text}}
  </div>
</template>

<script>
import {downloadPay} from 'api/pay'
export default {
  data () {
    return {
      text: ''
    }
  },
  created() {
    console.log('created')
    downloadPay('ireneou@bcis.net.cn').then((res) => {
      this.text = res
    })
    downloadPay('jaygao@bcis.net.cn').then((res) => {
      this.text = res
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $small-margin
</style>



